@font-face {
  font-family: "zg59";
  src: url("https://yizhuo-liang.github.io/resources/fonts/zg59.ttf") format("truetype");
}

.theBody {
  background-color: rgb(255, 255, 255);
}

.poster {
  margin: 0; margin-top: 5%; margin-left: 5%; width: 90%; height: auto; border: 0; padding-bottom: 4vh;
}

.text {
  width: 76vw;
  margin-left: 12vw;
  font-family: "zg59";
  font-size: large;
  color: rgb(5, 88, 123);
}

.right {
  text-align: right;
}

.box {
  border-radius: 4vw; box-shadow: 25px 25px 50px rgb(0, 0, 0, 0.35);
  background-color: rgb(214, 94, 93);
  width: 50vw;
  height: 1vw;
  margin-left: 25vw;
  margin-top: 4vh;
  margin-bottom: 8vh;
  color: white;
  font-size: x-large;
  text-align: center;
}